/**FONT ICON**/
@font-face {
    font-family: 'vnexpress-icon';
    src: url('fonts/vnexpress-icon.eot?y32xyr');
    src: url('fonts/vnexpress-icon.eot?y32xyr#iefix') format('embedded-opentype'),
        url('fonts/vnexpress-icon.ttf?y32xyr') format('truetype'),
        url('fonts/vnexpress-icon.woff?y32xyr') format('woff'),
        url('fonts/vnexpress-icon.svg?y32xyr#vnexpress-icon') format('svg');
    font-weight: normal;
    font-style: normal;
}
i {
    font-family: 'vnexpress-icon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    font-size: 15px;
    color: var(--gray2);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

/* Scss Document */
%ic {
    width: 14px;
    height: 14px;
    background: url(images/graphics/icons-video600s.svg) no-repeat;
    display: inline-block;
    vertical-align: middle;
}

.ic-user {
    background: url("images/graphics/icon_vne.svg") no-repeat;
    display: inline-block;
    vertical-align: middle;
    background-position: -611px -193px;
    width: 18px;
    height: 18px;
}

.ic-bell {
    background: url("images/graphics/icon_vne.svg") no-repeat;
    display: inline-block;
    vertical-align: middle;
    width: 18px;
    height: 18px;
    background-position: -252px -192px;
}

.ic-not-font {
    &:before {
        content: none !important;
    }
}

